class Dataformatter {

  constructor({ locale = 'ru' } = {}) {
    this.setLocale(locale);
  }

  setLocale(locale) {
    const localeData = require('./locales/' + locale + '.js');
    this.locale = localeData;
  }


};

let d = new Dataformatter();

console.log(d.localeData)



